.hero {
    background-image: url('../../../../public/images/banner.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 90vh;
    width: 100%;
}

.hero .container {
    padding-top: 15%;
}

.hero h1 {
    color: #fff;
    font-size: 60px;
}

.hero p {
    color: #fff;
    opacity: 0.8;
}

form {
    background-color: #fff;
    border-radius: 5px;
    margin-top: 50px;
    padding: 0 20px;
}

form input {
    padding: 10px;
    width: 100%;
    border: 1px solid rgba(128, 128, 128, 0.3);
    margin-top: 5px;
    border-radius: 5px;
}

form span {
    font-size: 14px;
    color: grey;
}

input::placeholder {
    font-size: 17px;
    color: black;
}

form .box {
    padding: 15px;
    border-left: 1px solid rgba(128, 128, 128, 0.2);
}

form .box:nth-child(1) {
    border-left: none;
}

form h3 {
    font-weight: 500;
}

@media screen and (max-width:700px) {
    .hero .container {
        padding-top: 30%;
    }

    form {
        display: flex;
        flex-wrap: wrap;
    }

    form .box {
        border-left: none;
    }

    form input {
        width: 100%;
    }
}